.container-area {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container-area {
    max-width: 90vw;
    padding: 5vh 0;
  }
}
@media (min-width: 1140px) {
  .container-area {
    padding: 20vh 0;
  }
}
@media (min-width: 1024px) {
  .container-area {
    max-width: 90vw;
  }
}
@media (min-width: 1440px) {
  .container-area {
    max-width: 85vw;
  }
}
@media (min-width: 2560px) {
  .container-area {
    max-width: 62.5vw;
  }
}
.first-block {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  box-sizing: border-box;
}
.first-block .column {
  flex: 1 1 320px;
  max-width: 384px;
}
.first-block .column + .column {
  margin-left: 24px;
}
@media (max-width: 664px) {
  .first-block .column + .column {
    margin-left: 0;
  }
}
.first-block .image-container {
  display: flex;
  flex-flow: row wrap;
  flex: 2 1 640px;
  margin-right: 24px;
  justify-content: center;
}
@media (max-width: 1140px) {
  .first-block .image-container {
    flex: 1 1 auto;
    margin-right: 0;
  }
  .first-block .text-container {
    max-width: 100%;
  }
}
.first-block img {
  transition: transform 1s ease;
}
.first-block img:hover {
  transform: scale(1.25);
}
.first-block hr {
  border-top: 1px solid white;
}
.first-block figure:not(:last-child) {
  margin-bottom: 20px;
}
.first-block .header {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 26px;
  font-weight: 300;
  letter-spacing: 0.5px;
  line-height: 36px;
}
.first-block .first-block-text {
  color: #c2c2c2;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 30px;
}
.first-block .aside-header {
  color: #cf1430;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 28px;
}
.first-block .aside-text {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 30px;
}
.first-block .zoom-image-container {
  overflow: hidden;
}
.first-block .columns {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  flex-flow: row wrap-reverse;
}
.block-image {
  width: 100%;
  max-width: 384px;
}
.second-block {
  margin-top: 228px;
}
.second-block .block-image {
  border-bottom: 4px solid transparent;
}
.second-block .block-image:hover {
  border-bottom-color: #cf1430;
  cursor: pointer;
}
.second-block .header {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 42px;
  font-weight: 300;
  letter-spacing: 0.81px;
  line-height: 57px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}
.second-block .block-header {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 30px;
}
.second-block .block-text {
  color: #c2c2c2;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
}
.second-block .block-end-text {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 30px;
}
.second-block .columns {
  display: grid;
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 384px));
  justify-content: center;
}
.second-block a {
  text-decoration: none;
  padding-bottom: 7px;
  border-bottom: 1px solid #cf1430;
}
.second-block a:hover {
  border-bottom-width: 2px;
}
body {
  background-color: #0e1414;
}
